<template lang="html">
  <el-dialog
    title="corn表达式"
    :visible="visible"
    @close="visible = false"
  >
    <div class="cron" :val="value_">
      <el-tabs v-model="activeName">
        <el-tab-pane label="秒" name="s">
          <second-and-minute v-model="sVal" lable="秒"></second-and-minute >
        </el-tab-pane>
        <el-tab-pane label="分" name="m">
          <second-and-minute v-model="mVal" lable="分"></second-and-minute >
        </el-tab-pane>
        <el-tab-pane label="时" name="h">
          <hour v-model="hVal" lable="时"></hour>
        </el-tab-pane>
        <el-tab-pane label="日" name="d">
          <day v-model="dVal" lable="日"></day>
        </el-tab-pane>
        <el-tab-pane label="月" name="month">
          <month v-model="monthVal" lable="月"></month>
        </el-tab-pane>
        <el-tab-pane label="周" name="week">
          <week v-model="weekVal" lable="周"></week>
        </el-tab-pane>
        <el-tab-pane label="年" name="year">
          <year v-model="yearVal" lable="年"></year>
        </el-tab-pane>
      </el-tabs>
      <!-- table -->
      <el-table
        :data="tableData"
        size="mini"
        border
        style="width: 100%;">
        <el-table-column
          prop="sVal"
          label="秒"
          width="70">
        </el-table-column>
        <el-table-column
          prop="mVal"
          label="分"
          width="70">
        </el-table-column>
        <el-table-column
          prop="hVal"
          label="时"
          width="70">
        </el-table-column>
        <el-table-column
          prop="dVal"
          label="日"
          width="70">
        </el-table-column>
        <el-table-column
          prop="monthVal"
          label="月"
          width="70">
        </el-table-column>
        <el-table-column
          prop="weekVal"
          label="周"
          width="70">
        </el-table-column>
        <el-table-column
          prop="yearVal"
          label="年">
        </el-table-column>
      </el-table>
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button @click="visible = false">取 消</el-button>
      <el-button type="primary" @click="okHandle">确 定</el-button>
    </span>
  </el-dialog>
  
</template>

<script>
import SecondAndMinute from './cron/secondAndMinute'
import hour from './cron/hour'
import day from './cron/day'
import month from './cron/month'
import week from './cron/week'
import year from './cron/year'
export default {
  props: {
    value: {
      type: String
    }
  },
  data() {
    return {
      activeName: 's',
      sVal: '',
      mVal: '',
      hVal: '',
      dVal: '',
      monthVal: '',
      weekVal: '',
      yearVal: '',
      visible: false
    }
  },
  watch: {
    'value'(a, b) {
      this.updateVal()
    }
  },
  computed: {
    tableData() {
      return [{
        sVal: this.sVal,
        mVal: this.mVal,
        hVal: this.hVal,
        dVal: this.dVal,
        monthVal: this.monthVal,
        weekVal: this.weekVal,
        yearVal: this.yearVal
      }]
    },
    value_() {
      if (!this.dVal && !this.weekVal) {
        return ''
      }
      if (this.dVal === '?' && this.weekVal === '?') {
        this.$message.error('日期与星期不可以同时为“不指定”')
      }
      if (this.dVal !== '?' && this.weekVal !== '?') {
        this.$message.error('日期与星期必须有一个为“不指定”')
      }
      const v = `${this.sVal} ${this.mVal} ${this.hVal} ${this.dVal} ${this.monthVal} ${this.weekVal} ${this.yearVal}`
      if (v !== this.value) {
        this.$emit('input', v)
      }
      return v
    }
  },
  methods: {
    updateVal() {
      if (!this.value) {
        return
      }
      const arrays = this.value.split(' ')
      this.sVal = arrays[0]
      this.mVal = arrays[1]
      this.hVal = arrays[2]
      this.dVal = arrays[3]
      this.monthVal = arrays[4]
      this.weekVal = arrays[5]
      this.yearVal = arrays[6]
    },
    okHandle() {
      this.$emit('ok', this.value_)
      this.visible = false
    },
    show() {
      this.visible = true;
    }
  },
  created() {
    this.updateVal()
  },
  components: {
    SecondAndMinute, hour, day, month, week, year
  }
}
</script>

<style lang="css" scoped>
.cron {
  padding: 10px;
}
</style>
